.authentication-modal {
	max-height: 100vh;
}
.authentication-modal .modal-dialog {
	margin: 0 !important;
	overflow-x: scroll;
	background: #fff;
	height: 100vh;
	max-width: 500px !important;
}
.authentication-modal.login .modal-dialog {
	overflow: hidden;
}
.authentication-modal.login {
	max-height: 100vh;
	height: 100vh;
}
.authentication-modal .modal-content {
	border-radius: 0 !important;
	border: 0 !important;
	height: 100%;
}
.login-form {
	padding-top: 60px;
}
.required-field {
	color: var(--bs-red);
	margin: 0 2px;
}
.checkbox-agree {
	display: flex;
	align-items: start;
	gap: 8px;
	color: #8a8a8a;
	font-size: 12px;
}
.checkbox-agree input {
	margin-top: 4px;
}
.tabs {
	display: flex;
	gap: 12px;
	justify-content: space-around;
	background: #effaff;
	border-radius: 50px;
	margin: auto;
	width: 90%;
	align-items: center;
	padding: 6px;
}
.tabs p {
	margin: 0;
	font-weight: 700;
	font-size: 20px;
	color: #838383;
	width: inherit;
	text-align: center;
	padding: 6px 0;
	min-height: 44px;
	cursor: pointer;
}
.tabs p.active {
	background-color: var(--primary-color);
	color: #fff;
	border-radius: 50px;
}
.login-form .title,
.signup-form .title {
	font-size: 24px;
	line-height: 100%;
	margin-bottom: 32px;
	font-weight: 700;
}
.login-form .form-control,
.signup-form .form-control {
	min-height: 42px;
	padding: 5px 14px;
}
.signup-form .title::after {
	width: 9rem;
}
.authentication-modal .login-btn:disabled,
.authentication-modal .signup-btn:disabled {
	background: #d7d7d7;
	border-color: #d7d7d7;
}
.authentication-modal .login-btn,
.authentication-modal .signup-btn {
	background-color: var(--primary-color);
	margin-top: 1rem;
	width: 100%;
	padding: 7.5px;
	font-weight: 700;
	border-radius: 32px;
}
.login-form .login-btn:hover,
.signup-form .signup-btn:hover {
	background-color: var(--primary-color);
	border-color: var(--primary-color);
	opacity: 0.8;
}

.signup,
.login {
	background: none;
	width: 100%;
	padding: 12px;
	color: #000;
	border: none;
}
.signup:hover,
.login:hover {
	background: none;
	color: #000;
}
.signup-btn span {
	margin-left: 1rem;
}
.authentication-modal .react-tel-input .flag-dropdown.open .selected-flag,
.authentication-modal .react-tel-input .flag-dropdown {
	background-color: #ecf9ff;
	border-right: none;
	width: 45px;
}
.authentication-modal .react-tel-input .form-control {
	width: 100%;
	border-radius: 0.5rem;
}
.authentication-modal .form-label {
	font-weight: 700;
}
.authentication-modal .forget {
	background: none;
	border: none;
	color: var(--primary-color);
	text-align: end;
	padding: 0;
	margin-top: 5px;
	text-decoration: underline;
}
.forget:hover {
	color: rgba(0, 0, 255, 0.57);
}
.showPassword {
	position: absolute;
	top: 2.6rem;
	cursor: pointer;
}
.en .showPassword {
	right: 25px;
	left: auto;
}
.ar .showPassword {
	left: 25px;
	right: auto;
}
@media only screen and (max-width: 500px) {
	.modal.show .modal-dialog {
		margin-top: 7rem;
	}
}
