.navbar {
	padding: 0 75px;
	width: 100%;
	height: 80px;
	box-shadow: 0px 0px 12px 0px #00000026;
}
.navbar .one {
	order: 1;
}
.navbar .one span {
	color: #8a8a8a;
	font-weight: 700;
	font-size: 20px;
	cursor: pointer;
}
.navbar .one span.active {
	color: var(--primary-color);
}
.navbar .two {
	order: 3;
}
.navbar .three {
	order: 2;
}
.active-user {
	position: absolute;
	top: 43%;
	width: 8px;
	height: 8px;
	background: green;
	border-radius: 50%;
	left: -14px;
}
.navbar .three button.registration-button,
.navbar .three button.login-button {
	border: 1px solid var(--primary-color);
	border-radius: 30px;
	padding: 12px 20px;
	font-size: 18px;
	font-weight: 700;
}
.navbar .three button.login-button {
	background: none;
	color: var(--primary-color);
}

@media only screen and (max-width: 500px) {
	.navbar {
		padding: 0.5rem 8px;
		height: 70px;
	}
	.hamburger {
		position: relative;
		width: 30px;
		height: 22px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		cursor: pointer;
		background: none;
		border: none;
		padding: 0;
		transition: transform 0.3s ease;
	}
	.navbar .hamburger span {
		display: block;
		height: 3px;
		width: 100%;
		background: #fff;
		border-radius: 2px;
		transition: all 0.6s ease;
	}
	/* Animation when menu is open */
	.navbar .hamburger.open span:nth-child(1) {
		transform: translateY(9px) rotate(45deg);
	}

	.navbar .hamburger.open span:nth-child(2) {
		opacity: 0;
	}

	.navbar .hamburger.open span:nth-child(3) {
		transform: translateY(-10px) rotate(-45deg);
	}
	.navbar .navbar-toggler:active,
	.navbar .navbar-toggler:focus {
		box-shadow: none;
		border: none;
	}
	.navbar .cart-num {
		right: -5px;
		top: -5px;
	}
	.ar .navbar {
		direction: rtl;
	}
	.ar .navbar-collapse.show img {
		transform: rotate(180deg);
	}
	.navbar-collapse.show img {
		display: block;
	}
	.en .navbar-collapse.show {
		left: 0;
		opacity: 1;
	}
	.ar .navbar-collapse.show {
		right: 0;
		opacity: 1;
	}
	.navbar-nav .nav-link {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.navbar .one {
		order: 1;
	}
	.navbar .one span {
		font-size: 16px;
	}
	.navbar .one span:nth-of-type(2) {
		margin: 0 4px;
	}
	.navbar .two {
		order: 3;
		padding: 0;
	}
	.navbar .two img {
		height: 35px;
		width: 65px;
	}
	.navbar .three {
		order: 2;
		padding: 0 0.5rem;
	}
	.navbar .three .gap-24 {
		gap: 10px !important;
	}
	.navbar .three button.registration-button,
	.navbar .three button.login-button {
		padding: 8px;
		font-size: 15px;
	}
	.navbar-toggler {
		z-index: 999;
		border-color: #fff;
	}
	.navbar-collapse {
		position: absolute;
		background: var(--primary-color);
		top: 66px;
		z-index: 99;
		opacity: 0;
		width: 100% !important;
		padding: 12px 20px;
		box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.2);
	}
	.navbar .nav-link {
		color: #fff !important;
	}
	.navbar .navbar-nav {
		gap: 0;
	}
	.navbar-collapse a {
		margin-bottom: 0;
		color: #fff;
	}
	.navbar .logo img {
		width: 50px;
		height: 50px;
	}
	.cart-logo {
		margin: 0 16px !important;
	}
}
