.header {
	height: calc(100vh - 80px);
	background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
		url("../../../assets/bg.jpg");
	background-repeat: round;
	position: relative;
}
.header .header-data {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
.en .header .header-data {
	width: 60%;
}
.header .header-title {
	color: #fff;
	font-weight: 700;
	font-size: 40px;
	line-height: 100%;
	text-align: center;
	text-wrap: nowrap;
	margin-bottom: 16px;
}
.en .header .header-title {
	text-wrap: wrap;
}
.header .header-description {
	color: #fff;
	font-weight: 400;
	font-size: 26px;
	line-height: 32px;
	text-align: center;
	margin-bottom: 0;
}
@media only screen and (max-width: 500px) {
	.header {
		height: 50vh;
	}
	.header .header-data {
		width: 90%;
	}
	.header .header-title {
		font-size: 26px;
		text-wrap: wrap;
		line-height: 1.5;
	}
	.header .header-description {
		font-size: 20px;
		line-height: 1.3;
	}
}
