.aboutUs {
	margin-top: 48px;
	margin-bottom: 40px;
}
.aboutUs img {
	width: 100%;
	height: 400px;
	border-radius: 30px;
	margin-bottom: 32px;
}

.aboutUs .description {
	color: #62636c;
}

.aboutUs .type-title {
	margin-bottom: 40px;
	font-size: 40px;
	font-weight: 700;
	color: #404040;
}
.aboutUs .type-title span {
	font-size: 72px;
	font-weight: 500;
	line-height: 86.54px;
	display: block;
	color: #000;
}
@media only screen and (max-width: 500px) {
	.aboutUs {
		padding: 1rem;
		margin-top: 20px;
		margin-bottom: 0;
	}
	.aboutUs .type-title span {
		font-size: 40px;
		line-height: 1;
	}
	.aboutUs .about-us {
		flex-direction: column;
	}
	.aboutUs .about-us .description {
		background: #f8f8f8;
		padding: 40px 20px;
		width: 100% !important;
	}
	.aboutUs .about-us .image {
		width: 100% !important;
		margin-top: 1rem;
	}
	.react-tel-input .form-control {
		padding-left: 50px;
	}
}
