.work {
	background: #f6f6f6;
	padding: 60px 0;
	margin-bottom: 80px;
	text-align: center;
}
.work h1 {
	font-weight: 700;
	font-size: 40px;
	text-align: center;
	margin-bottom: 70px;
}
.work .work-card {
	display: flex;
	align-items: center;
	gap: 20px;
	margin-bottom: 32px;
}
.work .work-card div {
	background: #fff;
	border-radius: 50%;
	padding: 16px;
	position: relative;
}
.work .work-card div::after {
	content: "";
	position: absolute;
	top: 0;
	height: 72px;
	width: 5px;
	background: #fff;
	border-radius: 3px;
}
.work .work-card:first-of-type div:after {
	background: var(--primary-color);
}
.ar .work .work-card div::after {
	right: -24px;
}
.en .work .work-card div::after {
	left: -24px;
}
.work .work-card img {
	width: 32px;
	height: 32px;
}
.work .work-card p {
	font-weight: 600;
	font-size: 24px;
	margin: 0;
	text-align: start;
	text-transform: capitalize;
}
.work .work-image {
	padding: 30px;
	position: relative;
}
.work .work-image::before,
.work .work-image::after {
	content: "";
	position: absolute;
	width: 150px;
	height: 130px;
	background: var(--primary-color);
	border-radius: 30px;
}
.ar .work .work-image::before {
	top: 0;
	left: 0;
	animation: animation1 10s infinite ease-in-out;
}
.en .work .work-image::before {
	top: 0;
	left: 0;
	animation: animation1En 10s infinite ease-in-out;
}
.work .work-image::after {
	bottom: 0;
	right: 0;
	animation: animation2 10s infinite ease-in-out;
}
@keyframes animation1 {
	0% {
		left: 0;
		right: calc(100% - 150px);
	}
	50% {
		left: 100%;
		right: 0;
	}
	100% {
		left: 0;
		right: calc(100% - 150px);
	}
}
@keyframes animation1En {
	0% {
		left: 0;
		right: calc(100% - 150px);
	}
	50% {
		left: calc(100% - 150px);
		right: 0;
	}
	100% {
		left: 0;
		right: calc(100% - 150px);
	}
}
@keyframes animation2 {
	0% {
		right: 0;
		left: calc(100% - 150px);
	}
	50% {
		right: calc(100% - 150px);
		left: 0;
	}
	100% {
		right: 0;
		left: calc(100% - 150px);
	}
}
.work .work-image img {
	border-radius: 20px;
	width: 100%;
	z-index: 9;
	position: relative;
}
@media only screen and (max-width: 500px) {
	.work {
		padding: 30px 0;
		margin-bottom: 30px;
	}
	.work h1 {
		font-size: 24px;
		margin-bottom: 32px;
	}
	.work .work-card img {
		width: 30px;
		height: 30px;
	}
	.work .work-card {
		margin-bottom: 16px;
	}
	.work .work-card div::after {
		content: none;
	}
	.work .work-card p {
		font-size: 18px;
	}
}
