.scroll-to-top {
	position: fixed;
	bottom: 60px;
	right: 60px;
	width: 80px;
	height: 80px;
	padding: 10px 15px;
	font-size: 28px;
	border-radius: 8px;
	border: none;
	cursor: pointer;
	color: #fff;
	background: var(--primary-color);
	z-index: 1000;
	animation: spineer 2s infinite;
}
@keyframes spineer {
	0% {
		box-shadow: 0 0 0 0 rgb(0, 134, 197, 0.99);
	}
	100% {
		box-shadow: 0 0 0 45px rgb(0, 134, 197, 0.01);
	}
}
