}
.en {
	font-family: "Cairo", sans-serif;
	direction: ltr;
}
.ar {
	direction: rtl;
	font-family: "Cairo", sans-serif;
	/* font-family: "Almarai-Bold", sans-serif; */
	font-optical-sizing: auto;
}
.custom-div {
	background-color: var(--primary-color);
	padding: 5rem 2rem;
	color: #ffffff;
}
.cursor-pointer {
	cursor: pointer;
}
.fw-bold {
	font-weight: 700 !important;
}
.custom-div h1 {
	margin-bottom: 0;
	font-weight: 600;
}

button {
	background-color: black;
	color: white;
	padding: 10px 20px;
	border: none;
	cursor: pointer;
}

/* Header Styles */
header {
	background-color: white;
	padding: 1rem 2rem;
}

header nav ul {
	list-style: none;
	display: flex;
	justify-content: space-around;
	margin: 0;
	padding: 0;
}

header nav ul li {
	margin: 0 1rem;
	color: black;
	cursor: pointer;
}

/* Featured Products Styles */
.featured-products {
	background-color: #2e7a9d;
	padding: 2rem;
}

.featured-products h2 {
	margin-bottom: 1rem;
}

.featured-products .products {
	display: flex;
	justify-content: center;
	gap: 1rem;
}

.featured-products .products .product {
	background-color: white;
	color: black;
	padding: 1rem;
	border-radius: 10px;
	text-align: center;
}

/* Best Selling Products Styles */
.best-selling-products {
	background-color: #3e9abd;
	padding: 2rem;
}

.best-selling-products h2 {
	margin-bottom: 1rem;
}

.best-selling-products .products {
	display: flex;
	justify-content: center;
	gap: 1rem;
}

.best-selling-products .products .product {
	background-color: white;
	color: black;
	padding: 1rem;
	border-radius: 10px;
	text-align: center;
}

/* Download Section Styles */
.download-section {
	background-color: #4ebadc;
	padding: 2rem;
}

.download-section h2 {
	margin-bottom: 1rem;
}

.download-section button {
	margin-top: 10px;
}
.btn-primary {
	background-color: var(--primary-color);
	border-color: var(--primary-color);
}
.btn-primary:hover {
	opacity: 0.8;
	background-color: var(--primary-color);
}
.paths {
	padding: 20px 0;
	margin-bottom: 30px;
}
.paths a {
	background: none !important;
	color: #333 !important;
	padding: 0 !important;
	text-decoration: none;
}
.paths .gt {
	margin: 0 6px;
}
.paths h1 {
	font-size: 24px;
}
.header-home .carousel-indicators button {
	width: 14px;
	height: 14px;
	background-color: #fff !important;
	border-radius: 50%;
}
.header-home .carousel-indicators button.active {
	background-color: #888;
}
.header-home .carousel-control-next,
.header-home .carousel-control-prev {
	display: none;
}

.scroll-animation div p {
	width: 100%;
	overflow: hidden;
}
.scroll-animation:hover div p {
	animation: scroll 5s infinite;
	animation-timing-function: ease-in-out;
}
@keyframes scroll {
	0% {
		translate: 0;
		overflow: visible;
	}
	100% {
		translate: -100%;
		overflow: visible;
	}
}
.error-page {
	margin-top: 80px;
	margin-bottom: 100px;
}
.error-page h1 {
	font-size: 72px;
	font-weight: 500;
	margin-bottom: 20px;
}
.error-page h4 {
	font-size: 48px;
	font-weight: 400;
	margin-bottom: 24px;
}
.error-page .btn {
	font-size: 14px;
	width: 400px;
	height: 56px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: auto;
}
.error-page .btn:hover {
	border-color: var(--primary-color);
}
.no-data {
	text-align: center;
}
.no-data p {
	font-weight: 600;
	font-size: 22px;
}
.section-title {
	text-align: center;
	margin-bottom: 16px;
	position: relative;
}
.section-title::before,
.section-title::after {
	content: "";
	position: absolute;
	height: 3px;
	width: 100px;
	border-radius: 8px;
	background-color: #dedede;
	top: 47%;
}
.section-title::before {
	left: 33%;
}
.section-title::after {
	right: 33%;
}
.react-tel-input .form-control {
	padding-left: 50px;
}
.image-loader {
	width: 20px;
	height: 20px;
	border: 3px solid var(--primary-color);
	border-bottom-color: transparent;
	border-radius: 50%;
	box-sizing: border-box;
	animation: rotation 1s linear infinite;
}

@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
@media only screen and (max-width: 500px) {
	.section-title::before,
	.section-title::after {
		width: 60px;
	}
	.section-title::after {
		right: 8%;
	}
	.section-title::before {
		left: 8%;
	}
}

.accept-modal .modal-body {
	overflow-x: hidden;
}
.accept-modal .btn:disabled {
	background-color: var(--primary-color);
	border-color: var(--primary-color);
}
.accept-modal .modal-body {
	overflow: scroll;
	height: 77vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.accept-modal .rule label {
	font-size: 20px;
	font-weight: 600;
}

.accept-modal .modal-content {
	height: 90vh;
}
.gap-24 {
	gap: 24px !important;
}
.upload {
	border: 1px solid #dedede;
	border-radius: 8px;
	padding: 6px 15px;
	margin-top: 8px;
	position: relative;
}
.upload input {
	width: 100%;
	opacity: 0;
	z-index: 1;
	position: relative;
	cursor: pointer;
}
.upload svg,
.upload p {
	margin: 0;
	position: absolute;
	top: 50%;
	cursor: pointer;
}
.upload .image-loader {
	margin: 0;
	position: absolute;
	cursor: pointer;
	top: 25%;
}
.upload .svg,
.upload p {
	transform: translate(0, -50%);
}
.ar .upload svg,
.ar .upload .image-loader {
	left: 10px;
	transform: translate(0, -50%);
}
.en .upload svg,
.en .upload .image-loader {
	right: 10px;
	transform: translate(0, -50%);
}
.primary-button {
	border: 1px solid var(--primary-color);
	background: var(--primary-color);
	color: #fff;
	transition: all 0.5 ease-in-out;
}
.primary-button:hover {
	transition: all 0.5 ease-in-out;
	background: none;
	color: var(--primary-color);
}
.primary-underline-animation,
.text-underline-animation {
	position: relative;
	display: inline-block;
}

.text-underline-animation::after {
	content: "";
	position: absolute;
	bottom: -12px;
	left: 50%;
	height: 2px;
	width: 0;
	background: #000;
	transform: translateX(-50%);
	animation: lineGrow 8s ease-in-out infinite;
}
.primary-underline-animation::after {
	content: "";
	position: absolute;
	bottom: -6px;
	left: 50%;
	height: 2px;
	width: 0;
	background: var(--primary-color);
	transform: translateX(-50%);
	animation: lineGrow 10s ease-in-out infinite;
}

@keyframes lineGrow {
	0% {
		width: 10%;
	}
	50% {
		width: 100%;
	}
	100% {
		width: 10%;
	}
}
