.services-section {
	text-align: center;
	padding: 64px 0;
}
.services-section .title {
	color: var(--primary-color);
	font-weight: 700;
	font-size: 40px;
	margin-bottom: 50px;
	text-align: center;
}
.services-section .service-card {
	box-shadow: 0px 0px 12px 0px #00000026;
	border-radius: 20px;
	overflow: hidden;
	margin: 0 16px;
	transition: all 0.5s ease-in-out;
}
.services-section .service-card:hover {
	transform: scale(1.1);
}
.services-section .service-card img {
	width: 100%;
	height: 250px;
	margin-bottom: 24px;
}
.services-section .service-card h2 {
	font-weight: 700;
	font-size: 32px;
	margin-bottom: 16px;
}
.services-section .service-card p {
	color: #8a8a8a;
	font-weight: 700;
	font-size: 14px;
	line-height: 100%;
	margin-bottom: 20px;
	padding: 0 36px;
	line-height: 1.5;
}
.services-section .service-card button {
	font-size: 18px;
	margin-bottom: 24px;
	padding: 12px 50px;
	border-radius: 50px;
	font-weight: 700;
}
.web-view {
	display: block;
}
@media only screen and (max-width: 500px) {
	.web-view {
		display: none;
	}
	.services-section {
		padding: 32px 0;
	}
	.services-section .title {
		font-size: 24px;
		margin-bottom: 32px;
	}
	.services-section .service-card {
		margin: 0 8px;
	}
	.services-section .service-card img {
		height: 150px;
		margin-bottom: 24px;
	}
	.services-section .service-card h2 {
		font-size: 18px;
	}
	.services-section .service-card p {
		padding: 0 8px;
	}
	.services-section .service-card button {
		font-size: 16px;
		margin: 0 8px 20px;
		padding: 16px;
	}
}
