footer {
	background: linear-gradient(92.1deg, #0086c5 -28.45%, #00082d 99.46%);
	color: #fff;
	padding-top: 48px;
	padding-bottom: 22px;
}

footer .contacts-title {
	font-size: 16px;
	font-weight: 700;
	line-height: 100%;
	margin-bottom: 1rem;
}
footer .contacts p {
	font-size: 16px;
	font-weight: 400;
	line-height: 100%;
	margin-bottom: 14px;
	display: flex;
	gap: 4px;
	align-items: center;
}
footer .description {
	width: 75%;
	margin: auto;
}
footer .description img {
	margin-bottom: 12px;
}
footer .description p {
	font-size: 10px;
	line-height: 15px;
	font-weight: 400;
}
footer .follow {
	font-size: 18px;
	line-height: 21.64px;
	margin-bottom: 24px;
}
footer .icons {
	display: flex;
	gap: 16px;
}
footer .links a {
	display: block;
	margin-bottom: 14px;
	color: #fff;
	font-size: 16px;
	line-height: 100%;
	text-decoration: none;
	font-weight: 400;
}
footer .contacts a:hover,
footer .links a:hover {
	text-decoration: underline;
}
footer .contacts a {
	color: #fff;
	font-size: 16px;
	text-decoration: none;
}
footer .links .address {
	margin-top: 32px;
	font-size: 12px;
	line-height: 18px;
	display: flex;
	gap: 10px;
}
footer .links .address span {
	display: block;
	width: fit-content;
}
footer .power {
	font-size: 14px;
	line-height: 21px;
	margin-top: 6px;
	padding-bottom: 8px;
}
footer .power a {
	font-size: 16px;
	line-height: 19.23px;
}
footer .one {
	order: 1;
}
footer .two {
	order: 2;
}
footer .three {
	order: 3;
}
footer .four {
	order: 4;
}
.fixed-icon {
	position: fixed;
	right: 60px;
	bottom: 18px;
	z-index: 99999;
}
.fixed-icon svg {
	width: 50px;
	height: 50px;
}
@media only screen and (max-width: 500px) {
	footer {
		padding: 24px 20px 16px;
		margin-top: 0;
	}
	footer .description {
		width: 95%;
	}
	footer .one {
		order: 1;
		margin-bottom: 1rem;
		text-align: center;
	}
	footer .two {
		order: 3;
		text-align: center;
	}
	footer .three {
		order: 2;
		margin-top: 1rem;
	}
	footer .icons {
		justify-content: start !important;
	}
	footer .visa {
		margin-bottom: 10px;
		margin-top: 1rem !important;
	}
	footer .links a,
	footer .contacts-title {
		margin-bottom: 12px;
	}
	footer .three {
		margin-top: 0;
	}
	footer .follow {
		text-align: start !important;
	}
	footer .contacts p {
		justify-content: center;
	}
	footer .contacts-title,
	footer .social {
		text-align: center;
	}
	footer .visa {
		margin: 0 !important;
	}
}
.footer-animation {
	height: 3px;
	background: linear-gradient(
		50deg,
		var(--bg-primary-color) 0%,
		white 50%,
		var(--bg-primary-color) 100%
	);
	width: 80%;
	margin: 0.75rem auto;
	animation: pulse-line 6s ease-in-out infinite;
}
@keyframes pulse-line {
	0%,
	100% {
		-webkit-transform: scalex(0.3);
		-moz-transform: scalex(0.3);
		-o-transform: scalex(0.3);
		transform: scalex(0.3);
		opacity: 0.4;
	}
	50% {
		-webkit-transform: scalex(1);
		-moz-transform: scalex(1);
		-o-transform: scalex(1);
		transform: scalex(1);
		opacity: 1;
	}
}
